        if (header.order === '0') {
          // 说明想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '1') {
          // 说明之前是升序，想要降序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '2'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '2') {
          // 说明之前是降序，想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        }
        headerList.push(item)
      }
      if (header.order === '0') {
        // 说明想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      } else if (header.order === '1') {
        // 说明之前是升序，想要降序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, false))
      } else if (header.order === '2') {
        // 说明之前是降序，想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      }
      if (type === 'stock-zy-jgfx') {
        // 这个来分辨哪个表格需要排序
        this.jgfxTableData.tableHeader = headerList
        this.jgfxTableData.tableContent = tableContent
      } else if (type === 'stock-zgs-kcfx') {
        this.zgsKcfxTableData.tableHeader = headerList
        this.zgsKcfxTableData.tableContent = tableContent
      } else if (type === 'stock-zgs-jgfx') {
        this.zgsJgfxTableData.tableHeader = headerList
        this.zgsJgfxTableData.tableContent = tableContent
      }
    },
    changeTableRow ({row, name, type}) {
      // 当点击了表格的某一行，进行联动操作
      this.zgsqsfxTitle = '趋势分析-' + name
      this.zgsjgfxTitle = '结构分析-' + name
      this.getZgsQsfxLineData(name)
      this.getZgsJgfxBarLineData(name)
    },
    getZyBarLineOption (arr) {
      const option = {
        'legend': {
          show: true,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        'xAxis': [
          {
            'type': 'category',
            'name': '',
            interval: 0,
            nameLocation: 'start',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            'data': [],
            'axisPointer': {
              'type': 'shadow'
            },
            'axisLabel': {
              'show': true,
              interval: 0,
              'textStyle': {
                'color': '#71768C'
              },
              fontSize: px2px(24)
            },
            'axisLine': {
              'show': false
            },
            axisTick: {
              'show': false
            }
          }
        ],
        'yAxis': [
          {
            'type': 'value',
            'name': '(天)',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'axisLabel': {
              'show': true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              }
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: false,
              'lineStyle': {
                'color': '#7d838b'
              }
            },
            axisTick: {
              'show': false
            }
          },
          {
            'type': 'value',
            'name': '控制差异率',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'show': true,
            'axisLabel': {
              show: true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              },
              formatter: '{value}%'
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: true,
              lineStyle: {
                color: '#213A6B'
              }
            }
          }
        ],
        series: [
          {
            type: 'bar',
            name: '周转天数',
            barWidth: px2px(40),
            itemStyle: {
              barBorderRadius: [px2px(20), px2px(20), 0, 0],
              color: function (params) {
                if (params.name === '直营') {
                  return '#34C9F8'
                } else {
                  return '#326DFF'
                }
              }
            },
            data: []
          },
          {
            type: 'line',
            name: '控制差异率',
            yAxisIndex: 1,
            smooth: true,
            symbol: 'none',
            itemStyle: {
              color: '#FFBE14' // 图例的颜色
            },
            lineStyle: {
              color: '#FFBE14',
              type: 'solid',
              width: px2px(4)
            },
            data: []
          },
          {
            type: 'line',
            name: '差异率',
            yAxisIndex: 1,
            smooth: true,
            symbol: 'none',
            itemStyle: {
              color: '#FF0200' // 图例的颜色
            },
            lineStyle: {
              color: '#FF0200',
              type: 'dashed',
              width: px2px(4)
            },
            data: []
          }]
      }
      const labelList = []
      const zztsList = []
      const zzcyList = []
      const cyList = []
      for (let item of arr) {
        labelList.push(item.label)
        zztsList.push(item.zzts)
        zzcyList.push(item.kzcy)
        cyList.push(item.cy)
      }
      option.xAxis[0].data = labelList
      option.series[0].data = zztsList
      option.series[1].data = zzcyList
      option.series[2].data = cyList
      return option
    },
    getJgfxBarLineOption (arr) {
      const option = {
        'legend': {
          show: true,
          top: px2px(20),
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          },
          data: ['库存占比', '销售占比']
        },
        grid: {
          top: px2px(60),
          left: ((this.showZy === true && this.zyJgfxTabIndex === 0) || (this.showZy === false && this.zgsJgfxTabIndex === 1)) ? px2px(100) : px2px(200)
        },
        'xAxis': [
          {
            'type': 'value',
            'name': '',
            interval: 0,
            'show': false,
            nameLocation: 'start',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            'data': [],
            'axisPointer': {
              'type': 'shadow'
            },
            'axisLabel': {
              'show': true,
              interval: 0,
              'textStyle': {
                'color': '#71768C'
              },
              fontSize: px2px(24)
            },
            'axisLine': {
              'show': false
            },
            axisTick: {
              'show': false
            }
          }
        ],
        'yAxis': [
          {
            'type': 'category',
            'axisLine': {
              show: false,
              lineStyle: {
                'color': '#ECEFF2'
              }
            },
            axisLabel: {
              show: true,
              textStyle: {
                'color': '#5F6D96',
                fontSize: px2px(28)
              },
              formatter: function (params) {
                let str = ''
                if (params.length > 8) {
                  str = params.substring(0, 8) + '\n' + params.substring(8)
                } else {
                  str = params
                }
                return str
              }
            },
            'splitLine': {
              show: false,
              'lineStyle': {
                'color': '#7d838b'
              }
            },
            axisTick: {
              'show': false
            }
          }
        ],
        series: [
          {
            type: 'bar',
            name: '库存占比',
            barWidth: px2px(32),
            barGap: 0,
            label: {
              normal: {
                position: 'right',
                formatter: '{c}%',
                'show': true,
                fontSize: px2px(24),
                'color': '#326DFF'
              }
            },
            itemStyle: {
              barBorderRadius: [0, px2px(20), px2px(20), 0],
              color: function (params) {
                return '#326DFF'
              }
            },
            data: []
          },
          {
            type: 'bar',
            name: '销售占比',
            barWidth: px2px(32),
            barGap: 0,
            label: {
              normal: {
                position: 'right',
                formatter: '{c}%',
                show: true,
                fontSize: px2px(24),
                'color': '#D1A226'
              }
            },
            itemStyle: {
              barBorderRadius: [0, px2px(20), px2px(20), 0],
              color: function (params) {
                return '#D1A226'
              }
            },
            data: []
          }]
      }
      const labelList = []
      const kczbList = []
      const xszbList = []
      let i = 0
      for (let item of arr) {
        i++
        labelList.unshift(item.type)
        kczbList.unshift(item.kb)
        xszbList.unshift(item.xb)
        if (i === 10) {
          break
        }
      }
      option.yAxis[0].data = labelList
      option.series[0].data = kczbList
      option.series[1].data = xszbList
      return option
    },
    getAllData () {
      this.getZyKcfxBarLineData()
      this.getZyQsfxBarLineData()
      this.getZyJgfxBarLineData()
      this.getZgsKcfxTableData()
    },
    async getZyKcfxBarLineData () {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stock.getZyKcfxBarLineData, {dateStrM: this.dateStrM})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        for (let item of result.res) {
          const obj = {}
          obj.label = item.examination_subject
          obj.zzts = initNum(item.zz_rate)
          obj.kzcy = initNum(item.diff_rate) * 100
          obj.cy = initNum(item.target_diff_rate) * 100
          list.push(obj)
        }
        this.zyBarLineData = list
      }
    },
    async getZyQsfxBarLineData () {
      let params = {
        examSub: '直营',
        dateStrM: this.dateStrM
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stock.getZyQsfxBarLineData, params)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        for (let item of result.res) {
          const obj = {}
          obj.label = item.inv_date
          obj.zzts = initNum(item.zz_rate)
          obj.kzcy = initNum(item.diff_rate) * 100
          obj.cy = initNum(item.target_diff_rate) * 100
          list.push(obj)
        }
        this.zyqsBarLineData = list
      }
    },
    // 直营库存结构分析的数据
    async getZyJgfxBarLineData () {
      let params = {
        examSub: '直营',
        dateStrM: this.dateStrM
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stock.getZyJgfxBarLineData, params)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.jgfxTableDataInit = result
        this.dealZyJgfxData()
      }
    },
    dealZyJgfxData () {
      this.zyJgfxIndex = this.zyJgfxTabIndex.toString() + this.zyJgfxBtnIndex.toString()
      const BarLineList = []
      const list = []
      let dataList = []
      if (this.zyJgfxIndex === '00') {
        dataList = this.jgfxTableDataInit.resDq || []
        this.jgfxTableData.tableHeader = this.dqTableHeader
      } else if (this.zyJgfxIndex === '01') {
        dataList = this.jgfxTableDataInit.resDm || []
        this.jgfxTableData.tableHeader = this.dmTableHeader
      } else if (this.zyJgfxIndex === '10') {
        dataList = this.jgfxTableDataInit.resXq || []
        this.jgfxTableData.tableHeader = this.xqTableHeader
      } else if (this.zyJgfxIndex === '11') {
        dataList = this.jgfxTableDataInit.resXm || []
        this.jgfxTableData.tableHeader = this.xmTableHeader
      }
      for (let item of dataList) {
        const obj = {}
        obj.type = item.type_name
        obj.xe = formatNumber(initNum(item.sale).toFixed(3), 1)
        obj.ke = formatNumber(initNum(item.inv).toFixed(3), 1)
        obj.xb = switchPercent(item.sale_rotio_rate)
        obj.kb = switchPercent(item.inv_rotio_rate)
        obj.pc = switchPercent(item.deviation)
        list.push(obj)
        BarLineList.push(obj)
      }
      this.jgfxBarLineData = BarLineList.sort(compareSort('kb', false))
      this.jgfxTableData.tableContent = list.sort(compareSort('ke', false))
    },
    // 子公司库存分析表格数据
    async getZgsKcfxTableData () {
      let params = {
        examSub: '直营',
        dateStrM: this.dateStrM
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stock.getZgsKcfxTableData, params)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.zgsKcfxTableDataInit = result
        this.dealZgsKcfxTableData()
      }
    },
    dealZgsKcfxTableData () {
      const list = []
      for (let item of this.zgsKcfxTableDataInit.res) {
        const obj = {}
        obj.zgs = item.distribution_name
        obj.qty = formatNumber(initNum(item.inv_qty).toFixed(3), 1)
        obj.amt = formatNumber(initNum(item.inv_amt).toFixed(3), 1)
        obj.kzcy = switchPercent(item.diff_rate)
        obj.zzts = formatNumber(initNum(item.zz_rate).toFixed(2), 0)
        obj.tb = switchPercent(item.inv_yoy)
        obj.zb = switchPercent(item.rotio_rate)
        list.push(obj)
      }
      if (list.length > 0) {
        this.zgsKcfxTableData.tableContent = list.sort(compareSort('amt', false))
      } else {
        this.zgsKcfxTableData.tableContent = []
      }
      // 获取第一条数据，查询下面的线图和店铺表格数据
      if (this.zgsKcfxTableData.tableContent.length > 0) {
        this.zgsqsfxTitle = '趋势分析-' + this.zgsKcfxTableData.tableContent[0].zgs
        this.zgsjgfxTitle = '店铺排名-' + this.zgsKcfxTableData.tableContent[0].zgs
        // 这里要设置表格第一行默认选中
        this.$eventBus.$emit('resetSelectedRow', {type: 'stock-zgs-kcfx', name: this.zgsKcfxTableData.tableContent[0].zgs})
        this.getZgsQsfxLineData(this.zgsKcfxTableData.tableContent[0].zgs)
        this.getZgsJgfxBarLineData(this.zgsKcfxTableData.tableContent[0].zgs)
      } else {
        this.zgsqsfxTitle = '趋势分析'
        this.zgsjgfxTitle = '店铺排名'
        this.qsfxzgsLineData = []
        this.zgsJgfxTableDataInit = []
        this.zgsJgfxTableData = []
      }
    },
    // 获取子公司，趋势分析的线图数据
    async getZgsQsfxLineData (companyName) {
      let params = {
        examSub: '直营',
        subName: companyName,
        dateStrM: this.dateStrM
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stock.getZgsQsfxLineData, params)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        for (let item of result.res) {
          const obj = {}
          obj.label = item.period_wid
          obj.kzcy = (initNum(item.diff_rate) * 100).toFixed(0)
          obj.zzts = formatNumber(initNum(item.zz_rate).toFixed(2), 0)
          obj.cy = (initNum(item.target_diff_rate) * 100).toFixed(0)
          list.push(obj)
        }
        if (list.length > 0) {
          this.qsfxzgsLineData = list
        } else {
          this.qsfxzgsLineData = []
        }
      }
    },
    // 子公司库存结构分析获取数据
    async getZgsJgfxBarLineData (companyName) {
      let params = {
        examSub: '直营',
        subName: companyName,
        dateStrM: this.dateStrM
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.stock.getZgsJgfxBarLineData, params)
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.zgsJgfxTableDataInit = result
        this.dealZgsJgfxData()
      }
    },
    dealZgsJgfxData () {
      this.zgsJgfxIndex = this.zgsJgfxTabIndex.toString() + this.zgsJgfxBtnIndex.toString()
      const BarLineList = []
      const list = []
      let dataList = []
      if (this.zgsJgfxIndex === '00') {
        dataList = this.zgsJgfxTableDataInit.resShopq || []
        this.zgsJgfxTableData.tableHeader = this.shopqTableHeader
      } else if (this.zgsJgfxIndex === '01') {
        dataList = this.zgsJgfxTableDataInit.resShopm || []
        this.zgsJgfxTableData.tableHeader = this.shopmTableHeader
      } else if (this.zgsJgfxIndex === '10') {
        dataList = this.zgsJgfxTableDataInit.resDq || []
        this.zgsJgfxTableData.tableHeader = this.dqTableHeader
      } else if (this.zgsJgfxIndex === '11') {
        dataList = this.zgsJgfxTableDataInit.resDm || []
        this.zgsJgfxTableData.tableHeader = this.dmTableHeader
      } else if (this.zgsJgfxIndex === '20') {
        dataList = this.zgsJgfxTableDataInit.resSpuq || []
        this.zgsJgfxTableData.tableHeader = this.spuqTableHeader
      } else if (this.zgsJgfxIndex === '21') {
        dataList = this.zgsJgfxTableDataInit.resSpum || []
        this.zgsJgfxTableData.tableHeader = this.spumTableHeader
      }
      for (let item of dataList) {
        const obj = {}
        obj.type = item.type_name
        obj.xe = formatNumber(initNum(item.sale), 2)
        obj.ke = formatNumber(initNum(item.inv), 2)
        obj.xb = switchPercent(item.sale_rotio_rate)
        obj.kb = switchPercent(item.inv_rotio_rate)
        obj.pc = switchPercent(item.deviation)
        list.push(obj)
        BarLineList.push(obj)
      }
      this.zgsJgfxBarLineData = BarLineList.sort(compareSort('kb', false))
      this.zgsJgfxTableData.tableContent = list.sort(compareSort('ke', false))
    }
  },
  /**
  watch: {
    dateStr: {
      handler () {
        this.getAllData()
      }
    }
  }, **/
  created () {
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    this.$store.commit('updateHeaderTitle', '库存')
    this.$store.commit('updateShowIcon', true)
    this.$store.dispatch('updateUserOption', {USER_ACCOUNT: window.userAccount, EXAMINATION_SUBJECT: '直营', LEVEL: '总监', URL: '库存'})
    if (this.dateStr !== '') {
      this.getAllData()
    }
  },
  mounted () {
    this.$eventBus.$on('userSelected', this.getAllData)
  },
  beforeDestroy () {
    this.$eventBus.$off('userSelected', this.getAllData)
  },
  components: {
    pageTitle,
    SwitchBtnList,
    MinTitle,
    BarLineChart,
    TableList,
    TypeList,
    Tabs
  }
}
</script>

